@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #f6ede4;
  background-image: url(./res/ui/Backdrop_1.svg);
  text-align: center;
  min-width: 600px;
  font-family: 'Amatic SC', cursive;
}

.hero {
  display: flex;
  opacity: 0;
  position: relative;
  height: 100vh;
  min-height: 600px;
  animation: fade-in 0.5s 0.1s forwards;
} 

.logo {
  opacity: 0;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 50px #000;
  transition: left 0.5s;
  animation: pop-in 1s 0.1s forwards;
}

@keyframes fade-in {
  0% {opacity: 0;}
  100%{opacity: 1;}
}

@keyframes pop-in {
  0% {opacity: 0; transform: translateY(80px)}
  40% {opacity: 1; transform: translateY(-30px)}
  100%{opacity: 1; transform: translateY(0px)}
}

.nav {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  bottom: 10%;
  font-size: 50px;
}

.nav-button {
  background: transparent;
  line-height: 40px;
  margin: 0 2% 0 2%;
}

.nav-button:hover {
  animation: tilt-shaking 0.2s infinite linear;
}

@keyframes tilt-shaking {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(2deg); }
  50% { transform: rotate(0deg); }
  75% { transform: rotate(-2deg); }
  100% { transform: rotate(0deg); }
}

#contacts {
  background-image: url(./res/ui/backdrop_2.jpg);
  margin-top: 80px;
  position: relative;
  box-shadow: 0 -10px 50px #555;
  text-align: center;
}

.pfp-container {
  display: flex;
  justify-content: center;
  width: 350px;
  height: 350px;
  border-radius: 50%;
  margin: 50px 0 50px 0;
  box-shadow: 0 0 50px #555;
  overflow: hidden;
}

.pfp {
  flex-shrink: 0;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
}

.contact-form {
  background-color: #f6ede4;
  position: relative;
  width: 400px;
  height: 300px;
  margin: 50px;
  padding: 10px 0 10px 0;
  border-radius: 35px;
  box-shadow: 0 -10px 50px #555;
}

.contact-section {
  display: flex;
  flex-wrap: wrap;
  width: 230px;
  margin-left: 60px;
  justify-content: center;
}

.description.subtitle {
  font-family: 'Herr Von Muellerhoff', cursive;
  font-weight: 100;
  font-size: 50px;
}

.contact-details {
  font-weight: bold;
  width: 150px;
  margin: 0.25em 0 0.25em 0;
  text-align: left;
  font-size: 1.5em;
}