#pictures {
    background-image: url(./../../res/ui/backdrop_2.jpg);
    position: relative;
    max-height: 700px;
    box-shadow: 0 0 200px #000;
    padding-bottom: 80px;
    text-align: center;
    overflow: hidden;
    transition: max-height 1s;
}

#pictures.expanded {
    max-height: 3000px;
}

.expand-toggle {
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
    color: #fff;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100px;
    font-family: 'Courier New', Courier, monospace;
    font-size: 5em;
}

.content.gallery {
    max-width: 1250px;
    margin: auto;
}

.image-card {
    position: relative;
    background-color: #f6ede4;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 250px;
    height: 300px;
    margin: 20px;
    border-radius: 30px;
    box-shadow: 0 10px 30px #000;
    transition: transform 0.5s;
    overflow: hidden;
}

.image-card:hover {
    transform: scale(1.05);
}

.image {
    flex-shrink: 0;
    min-width: 95%;
    max-height: 80%;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0 0px 10px #000;
}

.image-card.redirect-menu {
    flex-direction: column;
}

.link {
    background-color: #fffefd;
    display: flex;
    flex-grow: 0;
    justify-content: center;
    width: 90%;
    height: 30%;
    margin: 5% 0 5% 0;
    padding: 20px 0 20px 0;
    border-radius: 30px;
    font-weight: bold;
    font-size: 30px;
    transition: transform 0.5s;
}

.link:hover {
    transform: scale(1.1);
    box-shadow: 0 0 10px #fff;
}